// =============================================================================
// Metered Endpoint Analytics API
// =============================================================================
// GET /api/v1/metered/[endpointId]/analytics
// 
// Provides comprehensive analytics for a metered endpoint including:
// - Usage statistics (requests, payments, revenue)
// - Time-series data (hourly/daily breakdowns)
// - Error rates and status code breakdowns
// - Performance metrics (latency percentiles)
// - Payment method breakdown
// - Facilitator usage stats

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { getEndpoint, getUsageStats, getPaymentsForEndpoint } from '@/db/metered-endpoints';
import { getX402CallLogs } from '@/integrations/x402/x402-call-log';
import { getDb } from '@/db/client';
import { validateDateRange, validateGranularity } from '@/lib/endpoint-validation';

/**
 * GET /api/v1/metered/[endpointId]/analytics
 * 
 * Get analytics for a metered endpoint
 * 
 * Query parameters:
 * - startDate: ISO date string (default: 7 days ago)
 * - endDate: ISO date string (default: now)
 * - granularity: 'hour' | 'day' | 'week' (default: 'day')
 * - includeDetails: boolean (default: false)
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const { endpointId } = await params; // Next.js 15+ requires awaiting params
  const ctx = await extractRequestContext(request, `/api/v1/metered/${endpointId}/analytics`);
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    const { apiKey } = authResult;
    ctx.apiKeyId = apiKey.id;
    
    // Check if user has access to this endpoint (admin or endpoint owner)
    // For now, allow any authenticated user - can add ownership checks later
    if (apiKey.role !== 'admin') {
      // TODO: Add endpoint ownership check
      // For now, allow access
    }
    
    // Get endpoint
    const endpoint = await getEndpoint(endpointId);
    if (!endpoint) {
      return NextResponse.json(
        {
          error: 'Endpoint not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }
    
    // Parse query parameters
    const searchParams = request.nextUrl.searchParams;
    const startDateParam = searchParams.get('startDate');
    const endDateParam = searchParams.get('endDate');
    const granularityParam = searchParams.get('granularity') || 'day';
    const includeDetails = searchParams.get('includeDetails') === 'true';
    
    // Validate date range
    const dateValidation = validateDateRange(startDateParam, endDateParam);
    if (!dateValidation.valid) {
      return NextResponse.json(
        {
          error: 'Invalid date range',
          code: 'INVALID_DATE_RANGE',
          details: dateValidation.errors,
        },
        { status: 400 }
      );
    }
    
    // Validate granularity
    const granularityValidation = validateGranularity(granularityParam);
    if (!granularityValidation.valid) {
      return NextResponse.json(
        {
          error: 'Invalid granularity',
          code: 'INVALID_GRANULARITY',
          details: granularityValidation.errors,
        },
        { status: 400 }
      );
    }
    
    const startDate = startDateParam || new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString();
    const endDate = endDateParam || new Date().toISOString();
    const granularity = granularityParam as 'hour' | 'day' | 'week';
    
    // Get basic usage stats
    const usageStats = await getUsageStats(endpointId, startDate, endDate);
    
    // Get recent payments
    const recentPayments = await getPaymentsForEndpoint(endpointId, 100);
    
    // Get x402 call logs for detailed analytics
    const x402Logs = await getX402CallLogs(endpointId, {
      startDate,
      endDate,
      limit: 1000, // Get enough for analytics
    });
    
    // Calculate detailed metrics
    const metrics = calculateMetrics(x402Logs, recentPayments, usageStats, granularity);
    
    // Build response
    const response = {
      endpointId: endpoint.id,
      endpointName: endpoint.name,
      period: {
        startDate,
        endDate,
        granularity,
      },
      summary: {
        totalRequests: usageStats.totalRequests,
        totalPayments: usageStats.totalPayments,
        totalRevenue: usageStats.totalRevenue,
        averageResponseTime: usageStats.averageResponseTime,
        successRate: calculateSuccessRate(x402Logs),
        errorRate: calculateErrorRate(x402Logs),
      },
      timeSeries: metrics.timeSeries,
      breakdowns: {
        byStatus: metrics.byStatus,
        byFacilitator: metrics.byFacilitator,
        byPaymentMethod: metrics.byPaymentMethod,
        byErrorCode: metrics.byErrorCode,
      },
      performance: {
        latency: {
          p50: metrics.latency.p50,
          p95: metrics.latency.p95,
          p99: metrics.latency.p99,
          average: usageStats.averageResponseTime,
        },
      },
      ...(includeDetails && {
        recentPayments: recentPayments.slice(0, 10).map(p => ({
          id: p.id,
          txHash: p.txHash,
          amount: p.amount,
          fromAddress: p.fromAddress,
          verifiedAt: p.verifiedAt,
          facilitator: p.facilitator,
        })),
        recentLogs: x402Logs.slice(0, 20).map(log => ({
          id: log.id,
          timestamp: log.timestamp,
          status: log.status,
          facilitator: log.facilitator,
          amount: log.amount,
          latencyMs: log.latencyMs,
          errorCode: log.errorCode,
        })),
      }),
    };
    
    return NextResponse.json(response);
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

/**
 * Calculate detailed metrics from logs and payments
 */
function calculateMetrics(
  logs: any[],
  payments: any[],
  usageStats: any,
  granularity: 'hour' | 'day' | 'week'
) {
  // Time series data
  const timeSeries: Record<string, {
    requests: number;
    payments: number;
    revenue: string;
    errors: number;
    averageLatency: number;
  }> = {};
  
  // Breakdowns
  const byStatus: Record<string, number> = {};
  const byFacilitator: Record<string, number> = {};
  const byPaymentMethod: Record<string, number> = {};
  const byErrorCode: Record<string, number> = {};
  
  // Latency data for percentiles
  const latencies: number[] = [];
  
  // Process logs
  for (const log of logs) {
    // Time series
    const timeKey = getTimeKey(log.timestamp, granularity);
    if (!timeSeries[timeKey]) {
      timeSeries[timeKey] = {
        requests: 0,
        payments: 0,
        revenue: '0',
        errors: 0,
        averageLatency: 0,
      };
    }
    
    timeSeries[timeKey].requests++;
    if (log.status === 'success') {
      timeSeries[timeKey].payments++;
      const amount = BigInt(log.amount || '0');
      const currentRevenue = BigInt(timeSeries[timeKey].revenue);
      timeSeries[timeKey].revenue = (currentRevenue + amount).toString();
    }
    if (log.status === 'failed') {
      timeSeries[timeKey].errors++;
    }
    
    // Latency
    if (log.latencyMs !== undefined && log.latencyMs !== null) {
      latencies.push(log.latencyMs);
      const currentAvg = timeSeries[timeKey].averageLatency;
      const count = timeSeries[timeKey].requests;
      timeSeries[timeKey].averageLatency = (currentAvg * (count - 1) + log.latencyMs) / count;
    }
    
    // Breakdowns
    byStatus[log.status] = (byStatus[log.status] || 0) + 1;
    byFacilitator[log.facilitator || 'UNKNOWN'] = (byFacilitator[log.facilitator || 'UNKNOWN'] || 0) + 1;
    
    if (log.errorCode) {
      byErrorCode[log.errorCode] = (byErrorCode[log.errorCode] || 0) + 1;
    }
  }
  
  // Process payments for payment method breakdown
  // Note: We can't distinguish session vs payment from logs alone
  // This would need to be tracked in usage_logs
  byPaymentMethod['payment'] = payments.length;
  
  // Calculate latency percentiles
  latencies.sort((a, b) => a - b);
  const p50 = latencies.length > 0 ? latencies[Math.floor(latencies.length * 0.5)] : 0;
  const p95 = latencies.length > 0 ? latencies[Math.floor(latencies.length * 0.95)] : 0;
  const p99 = latencies.length > 0 ? latencies[Math.floor(latencies.length * 0.99)] : 0;
  
  return {
    timeSeries,
    byStatus,
    byFacilitator,
    byPaymentMethod,
    byErrorCode,
    latency: {
      p50,
      p95,
      p99,
    },
  };
}

/**
 * Get time key for grouping
 */
function getTimeKey(timestamp: string, granularity: 'hour' | 'day' | 'week'): string {
  const date = new Date(timestamp);
  
  switch (granularity) {
    case 'hour':
      return date.toISOString().slice(0, 13) + ':00:00Z';
    case 'day':
      return date.toISOString().slice(0, 10) + 'T00:00:00Z';
    case 'week':
      const weekStart = new Date(date);
      weekStart.setDate(date.getDate() - date.getDay());
      return weekStart.toISOString().slice(0, 10) + 'T00:00:00Z';
    default:
      return date.toISOString().slice(0, 10) + 'T00:00:00Z';
  }
}

/**
 * Calculate success rate
 */
function calculateSuccessRate(logs: any[]): number {
  if (logs.length === 0) return 0;
  const successes = logs.filter(log => log.status === 'success').length;
  return successes / logs.length;
}

/**
 * Calculate error rate
 */
function calculateErrorRate(logs: any[]): number {
  if (logs.length === 0) return 0;
  const errors = logs.filter(log => log.status === 'failed').length;
  return errors / logs.length;
}

